'use client'

import { getUser } from "@/services/getUser";
import { IUser } from "@/type/Types";
import { useEffect, useState } from "react";
import styles from './users.module.scss';

export default function UserClient() {
  const [users, setUsers] = useState<IUser[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const data = await getUser();
        setUsers(data);
      } catch (error) {
        console.error('Ошибка загрузки пользователей:', error);
      } finally {
        setIsLoading(false);
      }
    };
    fetchUsers();
  }, []);

  if (isLoading) {
    return <div className={styles.loading}>Загрузка пользователей...</div>;
  }

  return (
    <div className={styles.container}>
      <h1 className={styles.title}>Пользователи</h1>
      <div className={styles.tableContainer}>
        <table className={styles.table}>
          <thead>
            <tr>
              <th>Пользователь</th>
              <th>Email</th>
              <th>Роль</th>
              <th>Разрешения</th>
              <th>Действия</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user) => (
              <tr key={user.id} className={styles.tableRow}>
                <td>
                  <div className={styles.userCell}>
                    <div className={styles.userAvatar}>
                      {user.first_name?.[0]?.toUpperCase() || user.login[0].toUpperCase()}
                    </div>
                    <div className={styles.userInfo}>
                      <div className={styles.userName}>{user.first_name || user.login}</div>
                      <div className={styles.userLogin}>@{user.login}</div>
                    </div>
                  </div>
                </td>
                <td>{user.email}</td>
                <td>
                  <span className={styles.roleTag}>
                    {user.roles?.[0] || 'Пользователь'}
                  </span>
                </td>
                <td>
                  <div className={styles.permissionsList}>
                    {user.permissions?.map((permission, index) => (
                      <span key={index} className={styles.permissionTag}>
                        {permission}
                      </span>
                    ))}
                  </div>
                </td>
                <td>
                  <div className={styles.actions}>
                    <button className={styles.actionButton}>
                      Редактировать
                    </button>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
}